*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #232b2b;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background-color: #008080;
  width: 550px; 
  margin: 20px auto;
  border-radius: 10px;
}


li{list-style:none;width:100%;display:flex;justify-content:space-around;height:55px}li p.roundNumber{font-size:30px;color:black;font-family:sans-serif;display:inline;margin:0 10px 0 5px}li div.playerRoundCode{width:60%;height:45px;margin:5px 10px 5px 0;border:1px solid black;border-radius:15px;display:flex;justify-content:flex-end;align-items:center}li div.playerRoundCode span{height:30px;width:30px;display:inline-block;border-radius:50%;border:1px solid white;margin:10px 10px;cursor:pointer}li div.playerRoundCode span:last-child{margin-right:20px}li div.playerRoundCode.futureRound{opacity:0.2}li div.resultRoundCode{width:30%;margin:15px 15px 10px 5px;padding:5px;border:1px solid black;border-radius:15px;display:flex;justify-content:space-around;align-items:center}li div.resultRoundCode span{height:15px;width:15px;display:inline-block;border-radius:50%;border:1px solid white;margin:5px 5px}li div.resultRoundCode.futureRound{opacity:0.2}li:first-child{margin-bottom:10px}li:last-child{margin-top:10px}

header{display:flex;justify-content:space-between}header p{width:330px;display:inline-block;background-color:#004c4c;color:white;margin:15px auto 15px 10px;padding:5px 0;text-align:center;line-height:40px;font-size:25px;font-family:sans-serif;border:1px solid white;border-radius:10px}header div.secretCodeShow{width:330px;height:52px;display:flex;justify-content:flex-end;align-items:center;margin:15px auto 15px 10px;padding:5px 0;background-color:#004c4c;border:1px solid white;border-radius:10px}header div.secretCodeShow span{height:30px;width:30px;display:inline-block;border-radius:50%;border:1px solid white;margin:10px 10px}header div.secretCodeShow span:last-child{margin-right:30px}header button.newGame{height:35px;width:165px;display:inline-block;margin:25px auto 0 0;font-size:20px;font-family:sans-serif;border-radius:10px}header button.newGame.firstOpen{-webkit-animation:scale 1s linear infinite;animation:scale 1s linear infinite;border:5px solid red}@-webkit-keyframes scale{0%{transform:scale(1);border-color:orange}25%{transform:scale(1.15);border-color:yellow}50%{transform:scale(1);border-color:lightgreen}75%{transform:scale(0.85);border-color:green}}@keyframes scale{0%{transform:scale(1);border-color:orange}25%{transform:scale(1.15);border-color:yellow}50%{transform:scale(1);border-color:lightgreen}75%{transform:scale(0.85);border-color:green}}main{width:100%;height:680px;overflow:hidden;background-color:#006666}main ul{width:100%;display:flex;flex-direction:column-reverse}footer{display:flex;flex-wrap:wrap;align-items:center}footer .selectedColor{width:100%;height:30px;border-top:2px solid black;border-bottom:2px solid black;color:white;font-weight:bold;text-align:center;line-height:1.6;letter-spacing:1px;text-shadow:1px 1px 0 #000, -1px 1px 0 #000, 1px -1px 0 #000, -1px -1px 0 #000, 0px 1px 0 #000, 0px -1px 0 #000, -1px 0px 0 #000, 1px 0px 0 #000, 2px 2px 0 #000, -2px 2px 0 #000, 2px -2px 0 #000, -2px -2px 0 #000, 0px 2px 0 #000, 0px -2px 0 #000, -2px 0px 0 #000, 2px 0px 0 #000, 1px 2px 0 #000, -1px 2px 0 #000, 1px -2px 0 #000, -1px -2px 0 #000, 2px 1px 0 #000, -2px 1px 0 #000, 2px -1px 0 #000, -2px -1px 0 #000}footer .pickColor{width:330px;margin:10px;display:inline-block;border:1px solid black;border-radius:15px;padding:0px 5px}footer .pickColor span{height:25px;width:25px;display:inline-block;border-radius:50%;border:1px solid white;margin:10px 7px;cursor:pointer}footer button.checkRound{height:35px;width:165px;display:inline-block;margin-left:15px;font-size:20px;font-family:sans-serif;border-radius:10px}footer button.checkRound:disabled{background-color:rgba(200,0,0,0.7);color:slategray;cursor:not-allowed}.yellow{background-color:yellow}.red{background-color:red}.green{background-color:green}.blue{background-color:blue}.white{background-color:white}.orange{background-color:orange}.gray{background-color:gray}.pink{background-color:pink}.black{background-color:black}

